<template>
  <div class="doctorListBox">
    <ul class="doctorList" v-if="doctorDutyList.length!==0">
      <li class="doctorListItem" v-for="(item,index) in doctorDutyList" :key="index">
        <div class="avatar">
          <img :src="item.logoUrl" v-if="item.logoUrl!==null" alt>
          <img src="../../../assets/appointment/docList_avatar.png" v-else alt>
        </div>
        <div class="right">
          <!-- item.isExpert==1 专家号 0 普通号 -->
          <div class="name">
            {{item.doctName}}
            <!-- 专家号 -->
            <div class="tag" v-if="item.isExpert==1">
              <span class="expert">专家号</span>
            </div>
            <!-- 普通号 -->
            <div class="tag" v-else>
              <span class="normal">普通号</span>
            </div>
          </div>
          <div class="doctorLevel">{{item.jobTitle}}</div>
          <!-- 展示医生评分组件 -->
          <ShowRateStar :docRate="item.level"/>
        </div>
        <LittleButton
          :state="item.signstatus"
          :callBack="()=>{jumpFn(item.id,item.doctName,item.isExpert,item.jobTitle)}"
        />
      </li>
    </ul>
    <div class="docListNull" v-else>
      <img src="../../../assets/appointment/docList_null.png" class="docListNullImg" alt>
      <div class="text">啊哦，今天暂无医生值班</div>
      <div class="choiceOtherDate">请选择其他日期</div>
    </div>
  </div>
</template>

<script>
import LittleButton  from "@/components/littleButton";
import ShowRateStar from '@/components/showRateStar'

import store from "@/vuex/index";

export default {
  name: "doctorList",
  components: {
    LittleButton,
    ShowRateStar
  },
  // props: ["doctorList"],
  data() {
    return {
      docRate:4.6
    };
  },
  computed: {
    doctorDutyList() {
      return store.state.wmodule.curDoctorDutyList;
    }
  },
  methods: {
    jumpFn(doctorId, doctorName, doctorLevel, doctorJobTitle) {
      this.$router.push({
        name: "doctorDetails",
        params: {
          doctorId: doctorId,
          doctorName: doctorName,
          doctorLevel: doctorLevel,
          curDoctorJobTitle: doctorJobTitle
        }
      });
      // 在点击按钮跳转的时候将医生信息存储到VueX中
      store.commit("wmodule/changeDoctor", {
        doctorId: doctorId,
        doctorName: doctorName,
        doctorLevel: doctorLevel,
        curDoctorJobTitle: doctorJobTitle
      });
    }
  },
  created() {

  }
};
</script>

<style scoped lang="less">
.doctorListBox {
  padding-bottom: 0.49 * 2rem #fff;

  .doctorList {
    width: 100%;
    box-sizing: border-box;
    padding: 0.25 * 2rem 0.15 * 2rem 0.98rem;
    background: #fff;

    .doctorListItem {
      position: relative;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      overflow: hidden;
      padding-bottom: 0.14 * 2rem;
      border-bottom: 1px solid #f6f7f8;
      margin-top: 0.28rem;
      .avatar {
        float: left;
        width: 0.58 * 2rem;
        height: 0.66 * 2rem;
        overflow: hidden;
        border-radius: 4px;
        /*position: relative;*/

        img {
          /*position: absolute;*/
          display: block;
          width: 100%;
          height: 100%;
        }
      }

      .right {
        /*width: 100%;*/
        float: left;
        margin-left: 0.2 * 2rem;

        .name {
          font-family: PingFangSC-Regular, sans-serif;
          font-size: 0.15 * 2rem;
          color: #272727;
          line-height: 0.21 * 2rem;
          margin-bottom: 0.02 * 2rem;

          .tag {
            margin-left: 0.1rem;
            display: inline-block;
            height: 0.32rem;
            background: #f1f1fb;
            border-radius: 0.04rem;
            padding: 0.02rem 0.1rem;
            box-sizing: border-box;
            line-height: 0.28rem;
            span {
              vertical-align: middle;
              font-family: PingFangSC-Regular, sans-serif;
              font-size: 0.2rem;
              color: #272727;
              opacity: 0.8;
              &.expert {
                color: #f97d66;
              }
            }
          }
        }

        .doctorLevel {
          font-family: PingFangSC-Regular, sans-serif;
          color: #b1afaf;
          font-size: 0.1 * 2rem;
          line-height: 0.14 * 2rem;
        }
        .showRateStar{
          margin-top: .32rem;
        }
      }

      .button {
        position: absolute;
        right: 0.15 * 2rem;
        bottom: 0.14 * 2rem;
      }
      &:nth-of-type(1) {
        margin-top: 0;
      }
    }
  }
  .docListNull {
    background: #fff;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      margin-top: 0.2 * 2rem;
      display: block;
      width: 1.29 * 2rem;
      height: 1.62 * 2rem;
      transform: scale(0.8);
    }
    .text {
      font-family: "PingFangSC-Regular", sans-serif;
      font-size: 0.14 * 2rem;
      color: #272727;
      margin-top: 0.2 * 2rem;
    }
    .choiceOtherDate {
      font-family: "PingFangSC-Regular", sans-serif;
      font-size: 0.12 * 2rem;
      color: #272727;
      opacity: 0.8;
      margin-top: 0.06 * 2rem;
    }
  }
}
</style>



