<template>
  <div class="button">
    <button class="littleButton" @touchstart="touchDown" @touchend="touchEnd" @click="callBack" v-if="state">
      <span>预约</span>
    </button>
    <button class="littleButton unable" v-else>
      <span>约满</span>
    </button>
    <div class="mask" ref="mask"></div>
  </div>
</template>

<script>
export default {
  name: 'littleButton',
  props: {
    state: Number,
    callBack: Function
  },
  methods: {
    touchDown () {
      this.$refs.mask.style.display = 'block'
    },
    touchEnd () {
      this.$refs.mask.style.display = 'none'
      // this.$emit(jumpFn)
    }
  }
}
</script>

<style scoped lang="less">
  .button {
    display: inline-block;
    position: relative;
    width: .72*2rem;
    height: .3*2rem;
    user-select: none;

    .littleButton {
      padding: 0;
      width: 100%;
      height: .3*2rem;
      position: absolute;
      border: none;
      border-radius: .02*2rem;
      background: #3391E8;
      box-shadow: 0 0.04*2rem 0.06*2rem 0 rgba(45, 136, 219, 0.30);
      color: #fff;
      letter-spacing: 0.08rem;
      font-size: .14*2rem;
      font-family: PingFangSC-Regular, sans-serif;
      white-space: nowrap;
      &.unable{
        background: #f1f1f1;
        color:#B1AFAF ;
        box-shadow: none;
      }
      span{
        width: .64rem;
        padding: .12rem .38rem;
        line-height: normal;
      }
    }

    .mask {
      z-index: 10;
      display: none;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: #000;
      opacity: .3;
      border-radius: .02*2rem;
    }
  }

</style>



