<template>
  <div class="schedule">
    <SecondaryTitleBar :title="secondaryTitle"/>
  </div>
</template>

<script>
  import SecondaryTitleBar from '@/components/secondaryTitleBar'

  export default {
    name: 'departmentSchedule',
    data () {
      return {
        secondaryTitle: '科室排班信息'
      }

    },
    components: {
      SecondaryTitleBar
    }
  }
</script>

<style scoped lang="less">
  .schedule {
    width: 100%;
    margin-top: .1*2rem;
    background: #fff;

    .title {
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      width: 100%;
      height: .427*2rem;
      line-height: .427*2rem;
      position: relative;
      padding-left: .15*2rem;

      .bar {
        position: absolute;
        display: inline-block;
        width: .03*2rem;
        height: .16*2rem;
        background: #3391E8;
        border-radius: 2px;
        top: 50%;
        transform: translateY(-50%);
      }

      .text {
        margin-left: .1*2rem;
        font-family: PingFangSC-Medium, sans-serif;
        color: #272727;
        font-size: .16*2rem;
      }
    }
  }
</style>



