<template>
  <div class="aboutDepartment">
    <div class="title">
      <span class="bar"></span>
      <span class="text">关于微创专科</span>
    </div>
    <div class="content">
      <p class="text">微创介入术专门针对于治疗内脏血管瘤的一门新技术。特别是对肝血管瘤的治疗有独到之处，不需要剖腹和插导管。其方法是在多普勒彩色B超引导下，用特制的针直接从...</p>
      <div class="more">更多>></div>
    </div>
  </div>
</template>

<script>
  export default {}
</script>

<style scoped lang="less">
  .aboutDepartment {
    width: 100%;
    height: 1.29*2rem;
    background: #fff;

    .title {
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      width: 100%;
      height: .427*2rem;
      line-height: .427*2rem;
      position: relative;
      padding-left: .15*2rem;

      .bar {
        position: absolute;
        display: inline-block;
        width: .03*2rem;
        height: .16*2rem;
        background: #3391E8;
        border-radius: 2px;
        opacity: 0.8;
        top: 50%;
        transform: translateY(-50%);
      }

      .text {
        margin-left: .1*2rem;
        font-family: PingFangSC-Regular, sans-serif;
        color: #272727;
        font-size: .16*2rem;
      }
    }

    .content {
      box-sizing: border-box;
      height: .843*2rem;
      background: #fff;
      border-top: 1px solid #f6f7f8;
      padding: .073*2rem .15*2rem .14*2rem;
      position: relative;

      .text {
        font-family: PingFangSC-Regular, sans-serif;
        color: #272727;
        font-size: .12*2rem;
        line-height: .17*2rem;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
      }

      .more {
        position: absolute;
        right: .17*2rem;
        bottom: .15*2rem;
        color: #3391E8;
        font-family: PingFangSC-Regular, sans-serif;
        font-size: .11*2rem;
        line-height: .16*2rem;
      }
    }
  }
</style>



