<template>
  <div class="timeLine">
    <ul class="date-list">
      <li
        class="date-item"
        :class="index==num?'active':''"
        v-for="(item,index) in deptTimeList"
        :key="index"
        @click="()=>{clickFn(index,deptId,item.workTime)}"
        ref="dateList"
      >
        <span>{{item.workWeek}}</span>
        <span>{{item.showWorkTime}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import store from '@/vuex/index'
import { rejects } from 'assert'

export default {
  name: 'timeLine',
  data () {
    return {
      num: 0
    }
  },
  computed: {
    deptId () {
      return store.state.wmodule.curDeptId
    },
    deptName () {
      return store.state.wmodule.curDeptName
    },
    deptTimeList () {
      return store.state.wmodule.curTimeList
    }
  },
  created () {
    console.log(this.$route.params)
    const { deptId, deptName } = this.$route.params
    store.commit('wmodule/changeDept', {
      deptId: deptId,
      deptName: deptName
    })

    store.dispatch('wmodule/getDeptTime', this.deptId)
  },
  mounted () {
    console.log(this.deptId)
    store.dispatch('wmodule/getDoctorDuty', {
      deptId: this.deptId
      /* workTime:store.state.wmodule.curTimeList[0].workTime */
    })
  },
  updated () {},
  methods: {
    clickFn (index, deptId, workTime) {
      this.num = index
      // 测试环境workTime修改为2018-12-8
      // workTime = '2018-12-21'
      store.dispatch('wmodule/getDoctorDuty', {
        deptId: deptId,
        workTime: workTime
      })
    }
  }
}
</script>

<style scoped lang="less">
.timeLine {
  position: sticky;
  top: 0.44*2rem;
  background: #fff;
  border-top: 1px solid #f6f7f8;
  padding: 0.04*2rem 0.15*2rem 0;
  z-index: 10;

  .date-list {
    width: 100%;
    overflow: hidden;
    height: 0.45*2rem;
    display: flex;
    .date-item {
      flex: 1;
      /*margin-right: .15*2rem;*/
      /*display: inline-block;*/
      width: 0.32*2rem;
      padding-bottom: 0.07*2rem;
      border-bottom: 1px solid #fff;

      span {
        text-align: center;
        display: block;
        color: #b1afaf;
        font-size: 0.12*2rem;
        font-family: PingFangSC-Regular, sans-serif;
        line-height: 0.17*2rem;
      }

      &:nth-last-of-type(1) {
        margin-right: 0;
      }

      &.active {
        transition: border-bottom-color 0.3s ease;
        border-bottom: 1px solid #3391e8;

        span {
          color: #3391e8;
        }
      }
    }
  }
}
</style>



