<template>
  <div class="departmentDetails">
    <AboutDepartment/>
    <DepartmentSchedule/>
    <TimeLine/>
    <DoctorList />

  </div>
</template>

<script>
import AboutDepartment from "./aboutDepartment";
import DepartmentSchedule from "./departmentSchedule";
import TimeLine from "./timeLine";
import DoctorList from "./doctorList";

import store from '@/vuex/index'
export default {
  name: "departmentDetails",
  components: {
    AboutDepartment,
    DepartmentSchedule,
    TimeLine,
    DoctorList
  },
  data() {
    return {};
  },
  methods: {},

};
</script>

<style scoped>
.departmentDetails {
  background: #f6f7f8;
  padding-bottom: 0.49*2rem #fff;
}
</style>



